assignmentsTemplate.propTypes = propTypes

export default assignmentsTemplate

export const CampaignPageQuery = graphql`
  query getAllCampaigns {
    allcampagin: allCampaignNameSpace {
      edges {
        node {
          id
          namespace
          campaigns {
            id
            name
            contents {
              id
              name
              identity
            }
          }
        }
      }
    }
    assignments: allAssignment {
      edges {
        node {
          id
          postId
          title
          description
          author {
            name
            email
          }
          expiryDate
          actualUrl
          terms {
            categoryId
            name
          }
        }
      }
    }
  }
`



